@import "../../scss/variables";
@import "../../scss/mixins";

.RevenueVarianceTable {
  margin-bottom: 20px;
  @include mixer(107);
  table {
    thead {
      position: sticky;
      top: 0;
      z-index: 2;
      tr {
        th {
          vertical-align: middle;
        }
        &:nth-of-type(1) {
          th {
            &:nth-of-type(2) {
              z-index: 5;
              // left: 60px !important;
              // position: sticky;
            }
          }
        }
        &:nth-of-type(2) {
          th {
            &:nth-of-type(1) {
              z-index: 5;
            }
          }
        }
      }
    }
    tbody {
      :not(:nth-child(1), :nth-child(2)) {
        text-align: right;
      }
      tr {
        &:hover {
          td {
            background-color: #d4e7fb;
          }
        }
        td {
          background-color: #d4e7fb;
          text-overflow: ellipsis;
          overflow: hidden;
          max-width: 300px;
          &:has(.even) {
            background-color: #c5e8e8;
            text-overflow: ellipsis;
            overflow: hidden;
            max-width: 300px;
          }
          &:has(.odd) {
            background-color: #facbcb;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          &:has(.total) {
            background-color: #f2f29f;
            text-overflow: ellipsis;
            overflow: hidden;
          }
        }
        &:nth-of-type(1) {
          td {
            background-color: $tableSummaryGray !important;
          }
        }
      }
    }

    tr {
      th,
      td {
        min-width: 98px;
        max-width: 98px;
        &:nth-of-type(1) {
          min-width: 98px;
          max-width: 98px;
          position: sticky;
          left: 0;
          z-index: 1;
        }
        &:nth-of-type(2) {
          min-width: 250px;
          max-width: 250px;
          position: sticky;
          left: 98px;
          z-index: 1;
        }
      }
    }
    &:has(.expandedColumns) {
      tr {
        th,
        td {
          &:nth-of-type(3) {
            min-width: 180px;
            max-width: 180px;
            position: sticky;
            left: 345px;
            z-index: 1;
          }
          &:nth-of-type(4) {
            min-width: 100px;
            max-width: 100px;
            position: sticky;
            left: 525px;
            z-index: 1;
          }
          &:nth-of-type(5) {
            min-width: 150px;
            max-width: 150px;
            position: sticky;
            left: 625px;
            z-index: 1;
          }
        }
      }
    }
  }
}
.ActualTillRevenue .react-datepicker {
  max-height: 200px;
  overflow-y: scroll;
}
